<template>
  <v-container
    fluid
  >
    <base-material-card
      class="px-5 py-3"
      color="indigo"
      icon="mdi-vuetify"
    >
      <div
        height="220px"
        class="d-flex justify-space-between align-center mt-4"
      >
        <div
          class="d-flex justify-space-between"
          style="width: 50%"
        >
          <div class="d-flex justify-center align-center">
            <v-switch
              v-model="isAllPrint"
              hide-details
              class="mt-1"
              :label="isAllPrint ? $t('已开启全部打印'):$t('未开启全部打印')"
            />
          </div>

          <div class="mr-4">
            <v-text-field
              v-model="dates"
              hide-details
              :label="$t('日期筛选')"
              prepend-icon="mdi-calendar"
              style="max-width: 220px; "
              append-icon="mdi-close"
              @change="reviewZbon"
              @click:append="clear"
            />
          </div>
        </div>

        <div
          class="d-flex align-center justify-space-between"
          style="width: 50%; "
        >
          <div
            class="d-flex justify-space-between align-center ml-3"
            style="width: 100%"
          >
            <v-btn
              large
              color="teal lighten-2"
              width="120px"
              @click="hideTime();downloadPdf()"
            >
              {{ $t('导出PDF') }}
            </v-btn>
            <v-btn
              large
              color="green lighten-2"
              width="120px"
              @click="sendZBonToEmail"
            >
              {{ $t('发送ZBon到邮箱') }}
            </v-btn>

            <v-btn
              large
              color="primary"
              width="120px"
              @click="printZbonBySpan"
            >
              <v-icon>mdi-printer</v-icon>
              {{ $t('打印') }}
            </v-btn>
          </div>
        </div>
        <!--        </div>-->
      </div>
      <v-row>
        <v-col
          cols="12"
          md="6"
          sm="6"
        >
          <div style=" margin-top: 32px">
            <v-date-picker
              v-model="dates"
              range
              style="font-size: large; border-style: solid; border-color: #367aeb;"
              full-width
              :max="todayDate"
              locale="de"
            />
          </div>
        </v-col>

        <v-col
          cols="12"
          md="6"
          sm="6"
        >
          <div
            ref="pdf"
          >
            <v-card
              v-if="taxInfos.length>0"
            >
              <v-card-title
                class="text-center justify-center text-h3 font-weight-bold"
              >
                <div>{{ $t('统计') }}</div>
              </v-card-title>

              <div style="margin-left: 60px; margin-right: 60px; ">
                <v-divider/>
              </div>

              <div
                v-if="hide"
                class="py-6 px-6"
                style="margin-left: 40px; margin-right: 40px"
              >
                <div>Ausgewählter Zeitraum：{{ dates[0] }} ~ {{ dates[1] }}</div>
                <div>PDF eingestellte zeit：{{ nowTime }}</div>
              </div>

              <!--              <div class="py-6 px-6 pb-2" >-->
              <div
                style="margin-left: 40px; margin-right: 40px"
                class="py-6 px-6 "
              >
                <div>
                  <div class="d-flex justify-space-between text-subtitle-2 grey--text text--darken-2">
                    <div>{{ $t('Umsatz In Haus') }}{{ parseInt(taxInfos[1].taxRateGroupValue * 100) }}%</div>
                    <div>{{ taxInfos[1].taxGroupTotal|priceDisplay }}€</div>
                  </div>
                  <div class="d-flex justify-space-between text-subtitle-2 grey--text text--darken-2">
                    <div>{{ $t('Umsatz Ausser Haus') }}{{ parseInt(taxInfos[2].taxRateGroupValue * 100) }}%</div>
                    <div>{{ taxInfos[2].taxGroupTotal|priceDisplay }}€</div>
                  </div>
                  <v-divider class="mx-2 my-2"/>
                  <div class="d-flex justify-space-between font-weight-black grey--text text--darken-2">
                    <div>{{ $t('GesamtUmsatz') }}{{ parseInt(taxInfos[0].taxRateGroupValue * 100) }}%</div>
                    <div>{{ taxInfos[1].taxGroupTotal + taxInfos[2].taxGroupTotal |priceDisplay }}€</div>
                  </div>
                  <div class="d-flex justify-space-between grey--text text--darken-2">
                    <div>-{{ $t('MWST') }}</div>
                    <div>
                      {{
                        taxInfos[1].taxGroupTotal / (1 + taxInfos[1].taxRateGroupValue) * taxInfos[1].taxRateGroupValue + taxInfos[2].taxGroupTotal / (1 + taxInfos[2].taxRateGroupValue) * taxInfos[2].taxRateGroupValue |priceDisplay
                      }}€
                    </div>
                  </div>
                  <div class="d-flex justify-space-between grey--text text--darken-2">
                    <div>-{{ $t('Netto') }}</div>
                    <div>
                      {{
                        taxInfos[1].taxGroupTotal / (1 + taxInfos[1].taxRateGroupValue) + taxInfos[2].taxGroupTotal / (1 + taxInfos[2].taxRateGroupValue) |priceDisplay
                      }}€
                    </div>
                  </div>
                  <v-divider class="mx-2 my-2"/>
                  <div class="d-flex justify-space-between text-subtitle-2 grey--text text--darken-2">
                    <div>{{ $t('Umsatz In Haus') }}{{ parseInt(taxInfos[4].taxRateGroupValue * 100) }}%</div>
                    <div>{{ taxInfos[4].taxGroupTotal|priceDisplay }}€</div>
                  </div>
                  <div class="d-flex justify-space-between text-subtitle-2 grey--text text--darken-2">
                    <div>{{ $t('Umsatz Ausser Haus') }}{{ parseInt(taxInfos[5].taxRateGroupValue * 100) }}%</div>
                    <div>{{ taxInfos[5].taxGroupTotal|priceDisplay }}€</div>
                  </div>
                  <v-divider class="mx-2 my-2"/>
                  <div class="d-flex justify-space-between font-weight-black grey--text text--darken-2">
                    <div>{{ $t('GesamtUmsatz') }}{{ parseInt(taxInfos[3].taxRateGroupValue * 100) }}%</div>
                    <div>{{ taxInfos[4].taxGroupTotal + taxInfos[5].taxGroupTotal |priceDisplay }}€</div>
                  </div>
                  <div class="d-flex justify-space-between grey--text text--darken-2">
                    <div>-{{ $t('MWST') }}</div>
                    <div>
                      {{
                        taxInfos[4].taxGroupTotal / (1 + taxInfos[4].taxRateGroupValue) * taxInfos[4].taxRateGroupValue + taxInfos[5].taxGroupTotal / (1 + taxInfos[5].taxRateGroupValue) * taxInfos[3].taxRateGroupValue |priceDisplay
                      }}€
                    </div>
                  </div>
                  <div class="d-flex justify-space-between grey--text text--darken-2">
                    <div>-{{ $t('Netto') }}</div>
                    <div>
                      {{
                        taxInfos[4].taxGroupTotal / (1 + taxInfos[4].taxRateGroupValue) + taxInfos[5].taxGroupTotal / (1 + taxInfos[5].taxRateGroupValue) |priceDisplay
                      }}€
                    </div>
                  </div>
                  <v-divider class="mx-2 my-2"/>
                  <div class="d-flex justify-space-between font-weight-black grey--text text--darken-2">
                    <div>Nettoumsatz:</div>
                    <div>{{ totalInfos[0] }}€</div>
                  </div>
                  <div class="d-flex justify-space-between font-weight-black grey--text text--darken-2">
                    <div>Umsatzsteuer:</div>
                    <div>{{ totalInfos[1] }}€</div>
                  </div>
                  <div class="d-flex justify-space-between font-weight-black grey--text text--darken-2">
                    <div>Tagsumsatz:</div>
                    <div>{{ totalInfos[2] }}€</div>
                  </div>
                  <template v-for="(servantItem, idx) in servantInfo">
                    <div
                      :key="idx"
                      class="d-flex justify-space-between grey--text text--darken-2"
                    >
                      <div>Kellner:{{ servantItem.name }}</div>
                      <div>{{ servantItem.fTotal }}€</div>
                    </div>
                  </template>
                  <template v-for="(paidItem, index) in paidInfo">
                    <div
                      :key="index+ paidItem.paidName "
                      class="d-flex justify-space-between grey--text text--darken-2"
                    >
                      <div>{{ paidItem.paidName }}({{ paidItem.paidCount }})</div>
                      <div>{{ paidItem.fPaidTotal }}€</div>
                    </div>
                  </template>
                  <div class="d-flex justify-space-between font-weight-black grey--text text--darken-2">
                    <div>{{ $t('Stornogesamt') }}:</div>
                    <div>{{ totalInfos[3] }}€</div>
                  </div>
                  <div class="d-flex justify-space-between font-weight-black grey--text text--darken-2">
                    <div>{{ $t('Rabattgesamt') }}:</div>
                    <div>{{ totalInfos[4] }}€</div>
                  </div>
                </div>
              </div>
            </v-card>
          </div>
        </v-col>
      </v-row>
    </base-material-card>
  </v-container>
</template>

<script>
  import {
    getDailyZbon,
    getRestaurantInfo, PrinterAllZbon,
    printZbonBySpan, reprintZbon,
    sendZBonThroughEmail,
    showAllDatevSettings,
  } from '@/components/Utlis/api'
  import { formatDate, formatFullData } from '@/common/common'
  import moment from 'moment'
  import IKUtils from 'innerken-js-utils'
  import Utils from '../../components/Utlis/Utils'
  import html2canvas from 'html2canvas'
  import Jspdf from 'jspdf'

  import Zbon from '@/model/Zbon'
  import GlobalConfig from '@/GlobalSettings'

  export default {
    props: {
      id: {
        default: -1,
        type: Number,
      },
    },
    data: function () {
      return {
        Zbon: Zbon,

        printByDay: 1, // printByDay 1 or 0
        finishZbonTime: '',
        dates: [],
        printDates: [],
        taxInfos: [],
        taxItem: {
          consumeTypeName: '',
          taxGroupTotal: 0,
          taxRateGroupValue: 0.00,
        },
        totalInfos: [],
        servantInfo: [],
        paidInfo: [],
        isAllPrint: false,
        menu: false,
        loading: false,
        currentIndex: 0,
        servantId: this.id,
        servantData: {},
        infoList: [],
        search: '',
        editedItem: {},
        pdf: null,
        nowTime: '',
        hide: false,
      }
    },
    computed: {
      todayDate: function () {
        if (Date.parse(new Date()) > Date.parse(this.finishZbonTime)) {
          return moment(new Date()).add(-1, 'days').format('YYYY-MM-DD')
        } else {
          return moment(new Date()).add(-2, 'days').format('YYYY-MM-DD')
        }
      },
    },
    watch: {
      dates: {
        immediate: true,
        handler: function () {
          this.reviewZbon()
        },
      },
    },
    async mounted () {
      this.finishZbonTime = moment().format('YYYY-MM-DD') + ' 04:00:00'
      if (Date.parse(new Date()) > Date.parse(this.finishZbonTime)) {
        this.dates = [formatDate(-1), formatDate(-1)]
      } else {
        this.dates = [formatDate(-2), formatDate(-2)]
      }
    },
    methods: {
      print () {
        Utils.showConfirm('Bist du sicher?', 'Möchten Sie alle Datensätze drucken?', () => {
          PrinterAllZbon().then(() => {
            Utils.toast('Erfolgreich drucken!')

            this.$refs.zbon.reload()
          })
        })
      },
      async generateZBonPDF (item) {
        window.open(GlobalConfig.getEndPoint() + 'ZBon.php?op=exportPDF&id=' + item.id)
        // await hillo.get('', { id: item.id })
      },
      async outputPdf () {
        const selectedItemIds = this.$refs.zbon.$data.selectedItems.reduce((arr, i) => {
          arr.push(i.id)
          return arr
        }, []).join(',')
        console.log(selectedItemIds)
        this.Zbon.outputMultiplePDF(selectedItemIds)
      },

      printSingleItem (item) {
        Utils.showConfirm('Bist du sicher?', 'Möchten Sie dieser Rekord drucken?', () => {
          reprintZbon(item.id).then(() => {
            Utils.toast('Erfolgreich drucken!')
            this.$refs.zbon.reload()
          })
        })
      },

      clear () {
        this.dates = []
      },
      goBack () {
        this.$router.history.go(-1)
        // this.$router.push({ name: '桌子' })
      },
      printZbonBySpan () {
        console.log(this.printDates, 'this.printDates')
        if (this.printDates.length === 2) {
          Utils.showConfirm('Bist du sicher?', 'Möchten Sie Zbon drucken?', () => {
            printZbonBySpan(this.printDates, this.isAllPrint ? '0' : '1').then(() => {
              Utils.toast('Erfolgreich drucken!')
            })
          })
        }
      },

      async reviewZbon () {
        if (this.dates.length > 0) {
          if (this.dates.length === 1) {
            this.printDates = [this.dates[0], this.dates[0]]
          } else {
            if (Date.parse(this.dates[0]) > Date.parse(this.dates[1])) {
              [this.dates[0], this.dates[1]] = [this.dates[1], this.dates[0]]
            }
            this.printDates = [this.dates[0], this.dates[1]]
          }
          this.taxInfos = []
          this.totalInfos = []
          await getDailyZbon(this.printDates).then(res => {
            res.content.content.taxInfos.forEach(item => {
              this.taxItem = IKUtils.deepCopy(item)
              this.taxInfos.push(this.taxItem)
            })
            this.servantInfo = res.content.content.servantInfo
            this.paidInfo = res.content.content.paidInfo
            this.totalInfos.push(res.content.content.fTotalTe)
            this.totalInfos.push(res.content.content.fTotalTax)
            this.totalInfos.push(res.content.content.fTotal)
            this.totalInfos.push(res.content.fValue)
            this.totalInfos.push(res.content.fValue2)
          })
        }
      },
      async sendZBonToEmail () {
        const res = await getRestaurantInfo()
        const emailAddress = res.content[0].ZBonEmail

        if (!emailAddress) {
          Utils.showError('Die Email Address ist leer, bitte in Restaurant Info Page einstellen')
          return false
        }
        Utils.showConfirm('Bist du sicher?', 'Möchten Sie Zbon nach Email \n' + emailAddress + '\n senden?', () => {
          const data = {
            startDate: this.printDates[0],
            endDate: this.printDates[1],
            printByDay: this.isAllPrint ? 0 : 1, // 1 print by day,  0 not
          }
          sendZBonThroughEmail(data).then(() => {
            Utils.toast('Erfolgreich gesendet!')
          })
        })
      },

      async getDatevSettings () {
        const _data = await showAllDatevSettings().then(res => res.content)
        // this.datevForm = _data
        return _data
      },
      hideTime () {
        this.hide = true
      },
      async downloadPdf () {
        this.nowTime = formatFullData(0)
        await this.hideTime()
        const target = this.$refs.pdf
        console.log(target)
        html2canvas(target, {
          useCORS: true, // 当图片是链接地址时，需加该属性，否组无法显示图片
          imageTimeout: 0,
          scale: 2,
          width: 800,
          height: 841,
        })
          .then(canvas => {
            console.log(canvas)
            const contentWidth = canvas.width // 592px
            const contentHeight = canvas.height // 841px
            const pageHeight = contentWidth / 800 * 841
            let leftHeight = contentHeight
            // let offsetX = 100
            // let offsetY = 100
            let offsetX = 0
            let offsetY = 0
            const imgWidth = 592
            const imgHeight = 592 / contentWidth * contentHeight
            const pageData = canvas.toDataURL('image/jpeg', 1.0)
            // 第一个方向,第二个单位,第三个尺寸格式
            // landscape横向
            // let pdf = new jspdf('landscape', 'pt', 'a4')
            const pdf = new Jspdf('', 'pt', 'a4')
            if (leftHeight < pageHeight) {
              pdf.addImage(pageData, 'JPEG', 0, 0, imgWidth, imgHeight)
            } else {
              while (leftHeight > 0) {
                pdf.addImage(pageData, 'JPEG', offsetX, offsetY, imgWidth, imgHeight)
                leftHeight -= pageHeight
                offsetX -= 592
                offsetY -= 841
                if (leftHeight > 0) {
                  pdf.addPage()
                }
              }
            }
            const name = 'ZBon' + this.dates[0] + '_' + this.dates[1] + '.pdf' // 定义生成的pdf的文件名字
            pdf.save(name)
          })
        this.hide = false
      },
    },
  }
</script>

<style scoped>
  .cardItem {
    margin-top: 0px;
    display: flex;
    align-content: center;
  }
</style>
